.promo {
  padding: 35px 0;

  @media (min-width: $tablet-width) {
    padding-top: 69px;
    padding-bottom: 76px;
  }

  @media (min-width: $desktop-width) {
    padding: 0;
  }
}

.promo__wrapper {
  position: relative;
  display: flex;
  align-items: flex-end;
  min-height: 290px;
  background-image: linear-gradient(to top, rgba(104, 183, 56, 0.85), rgba(104, 183, 56, 0.85)), url("../img/cat-in-backpack.png");
  background-size: cover;
  background-position: center;
  background-repeat: no-repeat;

  &::before {
    content: "";
    position: absolute;
    width: 78px;
    height: 80px;
    background-image: url("../img/icon-gift.svg");
    background-repeat: no-repeat;
    top: 60px;
    left: 101px;

    @media (min-width: $tablet-width) {
      left: 77px;
    }

    @media (min-width: $desktop-width) {
      left: 85px;
    }
  }

  @media (min-width: $tablet-width) {
    justify-content: flex-end;
    align-items: center;
    min-height: 200px;
    background-image: linear-gradient(to top, rgba(104, 183, 56, 0.85), rgba(104, 183, 56, 0.85)), url("../img/cat-in-backpack-tablet.png");

    @media (min-resolution: 2dppx) {
      background-image: linear-gradient(to top, rgba(104, 183, 56, 0.85), rgba(104, 183, 56, 0.85)), url("../img/cat-in-backpack-tablet@2x.png");
    }
  }

  @media (min-width: $desktop-width) {
    min-height: 288px;
    align-items: flex-end;
    background-image: linear-gradient(to top, rgba(104, 183, 56, 0.85), rgba(104, 183, 56, 0.85)), url("../img/cat-in-backpack.png");

    @media (min-resolution: 2dppx) {
      background-image: linear-gradient(to top, rgba(104, 183, 56, 0.85), rgba(104, 183, 56, 0.85)), url("../img/cat-in-backpack@2x.png");
    }
  }
}

