.promo__text {
  font-family: "Arial", sans-serif;
  font-size: 16px;
  line-height: 20px;
  text-align: center;
  color: $white;
  margin: 0;
  padding: 141px 60px 43px;

  @media (min-width: $tablet-width) {
    max-width: 185px;
    text-align: left;
    padding: 50px 100px 50px;
  }

  @media (min-width: $desktop-width) {
    text-align: center;
    padding: 140px 42px 42px;
  }
}
